
body{
	background-color: #eee;
	margin: 0;
	padding: 20px 0;
	font-family: "Arial","Microsoft YaHei","黑体","宋体",sans-serif;
	color: #333;
	font-size: 14px;
}
#app{
	background-color: #fff;
	width: 1050px;
	margin: 0 auto;
}
@media screen and (max-width:600px){
#app{
		width: 100%;
}
}
@media print {
body{
		padding: 0;
}
}



